@import url(https://fonts.googleapis.com/css?family=Crimson+Text:400,400i,600,600i,700,700i|Roboto:300,300i,400,400i,500,500i,700,700i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:300,400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Crimson+Text:400,400i,600,600i,700,700i|Roboto:300,300i,400,400i,500,500i,700,700i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-Home {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.App-Home h1 {
    /* color: rgba(255, 255, 255, 0.931); */
    font-weight: 300;
    font-size: 5rem;
}
.App-HomeLayout {
  height: 100vh;
  width: 100vw;
  font-family: Roboto Slab;
  background-image: url(/LW001.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}
.App-HomeLayout p, ul, h4 {
  font-family: Roboto;
  font-size: 1rem;
}


.App-Login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  height: 100%;
  width: 20%;
  min-width: 250px;
}
.App-Signup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  height: 100%;
  width: 20%;
  min-width: 250px;
}
.App-Info {
  
}
h2, h3, h4, p {
    margin: 0;
    padding: 0;
}

h2 {
    font-size: 2.4rem;
    margin-bottom: 1rem;
}

h3 {
    font-weight: 300;
    font-size: 2rem;
    padding-bottom: 0.5rem;
}


.App-MainLayout {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    font-family: Roboto Slab;
    background-image: url(/LW001.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}

.App-MainLayout p, ul, h4 {
    font-family: Roboto;
    font-size: 1rem;
}
.nav-item {
  font-size: 1.3rem;
}

.nav-plus {
  font-size: 4rem;
}
.App-Footer {
  height: 40vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(53, 53, 53);
  color: white;
}
.App-Wrapper {
  min-height: 95vh;
  width: 95%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  background-color: rgba(255, 255, 255, 0.060);
}
.App-HowTo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 60%;
  min-width: 320px;
  min-height: 95vh;

}

.How-To-Wrapper {
  background-color: rgb(128, 126, 126);
}


.How-To-item{
  width: 30vw;
  min-width: 250px;
}

.How-To-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  background-color: rgb(46, 106, 92);
  /* background-image: url("https://images.unsplash.com/photo-1493606278519-11aa9f86e40a?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80"); */
  background-size: center;
  background-repeat: no-repeat;

}

.item-one {
  background-color: #DDF8E8;
  color: #424242
}

.item-two {
  background-color: #CDD5D1;
  color: #504f4f
}

.item-three {
  background-color: #B4A6AB;
  color: #525050
}

.item-four {
  background-color: #946E83;
  color: #d3d0d0
}

.item-five {
  background-color: #615055;
  color: #dad6d6
}

.App-Features {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 40%;
  min-width: 320px;
  min-height: 100vh;
}

.features-item {
  width: 30vw;
  min-width: 250px;
}
.App-Profile {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 25%;
  min-width: 200px; 
}

.App-Profile img {
  width: 150px;
}

.profile-info {
  display: flex;
  flex-direction: column;
  text-align: left;

}

.info-container {
}




.App-Task {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-around;
  text-align: left;
  width: 85%;
  height: 100%;
}

.left-bar {
  flex: 1 1;
  height: 100%;
  min-width: 300px;
  max-width: 600px;
}

.right-bar {
  flex: 1 1;
  height: 100%;
  min-width: 300px;
  max-width: 900px;
}

.text_processed {
  height: 100%;
}
.collection-heading {
    
}

.collection-item {
    max-height: 30%;
    min-width: 300px;
    width: 50%;
    margin: 1rem 0;
    text-align: left
}

.App-Details {
 width: 70%;

}

.details-info {
  text-align: left
}

.button-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
